import "./IntroCard.css"
import profilePic from '../../images/keithProfilePic.jpeg'
import { useEffect, useState } from "react";
import TextTransition, { presets } from "react-text-transition"

const quotes = ["Tech", "Car", "Pasta"]

function IntroCard() {

    const [index, setIndex] = useState(0);

    useEffect(() => {
        const lastIndex = quotes.length - 1;
        if (index < 0) {
            setIndex(lastIndex);
        }
        if (index > lastIndex) {
            setIndex(0);
        }
    }, [index, quotes]);

    useEffect(() => {
        let slider = setInterval(() => {
            setIndex(index + 1);
        }, 3000);
        return () => {
            clearInterval(slider);
        };
    }, [index]);

    return (
        <section className="hero">
            <div className="hero-center">

                <article className="hero-info">
                    <h1>
                        Hello There,<br /> I'm Keith üòÅ
                    </h1>
                </article>

                <article className='hero-images'>
                    <img src={profilePic} className='profile-img' alt='phone' />
                    <p>"Avid <TextTransition
                        text={quotes[index % quotes.length]}
                        springConfig={presets.gentle}
                        inline
                        noOverflow={true}
                    /> Enthusiast"</p>
                </article>

            </div>
        </section>
    )
}

export default IntroCard